import React, { useEffect, useReducer, useState } from "react";
import { navigate } from "./Router";

export default function Header({show}) {
    const [transparent, setTransparent] = useState(!show);
    const fullOpacityDistance = window.innerHeight*0.4;

    const scrollHandler = () => {
        if (show) return;
        let y = window.scrollY;
        if(y > fullOpacityDistance) {
            setTransparent(false);
        } else if (y < fullOpacityDistance) {
            setTransparent(true);
        }
    }

    useEffect(()=> {
        document.addEventListener("scroll", scrollHandler);
        return ()=> {
            document.removeEventListener("scroll", scrollHandler);
        }
    }, [])

    return (
        <div className={"header " + (transparent ? "seethrough" : "solid")}>
            <h1>Thijn IT</h1>
            <nav>
                <a href="/">home</a>
                <a href="/vaardigheden">vaardigheden</a>
                {/* <a href="/info">info</a> */}
                <a href="#contactform">contact</a>
            </nav>
        </div>
    )
}