import skills from "../skills.json";
import "../stylesheets/pages/skills.scss";
export default function Skills() {
    return <div className="page skills">
        <div className="skillWrapper">
        {
            skills.map(e => (
                <div className="skill card">
                    <h3>{e.name}</h3>
                    <div className="list">
                    {
                        e.skills.map(skill => 
                            <span>- {skill.name}</span>
                            )
                        }
                    </div>
                    {e.image ? <img alt="logo" src={process.env.PUBLIC_URL+"/logos/"+e.image} className="photo"/> : ""}
                </div>
            ))
        }
        </div>
    </div>
} 