import './App.css';
import Header from './components/Header';
import Route, { Router } from './components/Router';
import Home from './pages/Home';
import "./stylesheets/page.scss";
import Footer from './components/Footer';
import Info from './pages/Info';
import Contact from './pages/Contact';
import { useEffect, useState } from 'react';
import Skills from './pages/Skills';

function App() {
  const [show, setShow] = useState(window.location.pathname !== "/")

  const stateListener = () => {
    setShow(window.location.pathname !== "/")
    console.log(window.location.pathname !== "/")
  }

  useEffect(() => {
    window.addEventListener("popstate", stateListener);
    return () => {
      window.removeEventListener("popstate", stateListener);
    }
  }, [])

  return <>
  <Header show={show}/>
  <Router>
      <Route path="/"><Home /></Route>
      <Route path="/vaardigheden"><Skills /></Route>
      <Route path="/info"><Info/></Route>
      <Route path="/contact"><Contact/></Route>
  </Router>
  <Footer/>
</>
}

export default App;
